import React, { useContext, useEffect, useState } from 'react';
import { UserContext } from '../../context/User';
import './Collection.css';
import { Link } from 'react-router-dom';
import { APIURL } from '../inc/Config';

export default function Collection(props) {
    const [user, setUser] = useContext(UserContext);
    const [superhereosInformation, setSuperhereosInformation] = useState([]);
    const [loading, setLoading] = useState(true);
    const [noSaves, setNoSaves] = useState(true);

    //Saves Props Change
    useEffect(() => {
        if(props.saves.length > 0)
            fetchSuperhereosInformation(props.saves);
        else
            setNoSaves(false);
    }, [props.saves]);

    //FETCH EVERY SUPERHERO INFORMATION FROM API
    const fetchSuperhereosInformation = (saves) => {
        const superheros = [];
        saves.forEach(s => {
            fetch("https://www.superheroapi.com/api.php/222080678854229/" + s.superheroId)
                .then(res => res.json())
                .then(data => {
                    superheros.push(data);
                    checkIfAllAreLoaded(superheros);
                });
        });
    }

    //CHECK IF EVERY SUPERHERO INFORMATION IS LOADED
    const checkIfAllAreLoaded = (s) => {
        if(s.length === props.saves.length)
            setSuperhereosInformation(s);
    } 

    //Set Loading WHEN FETCHING SUPERHEREOSINFO IS DONE
    useEffect(() => {
        if(superhereosInformation.length === props.saves.length && superhereosInformation.length > 0) {
            setLoading(false);
        }
    }, [superhereosInformation]);

    const imageClicked = id => {
        if(document.getElementById("removeBtn" + id).style.display === "none") {
            document.getElementById("removeBtn" + id).style.display = "inline-block";
            document.getElementById("image" + id).style.filter = "brightness(40%)";
        } else {
            document.getElementById("removeBtn" + id).style.display = "none";
            document.getElementById("image" + id).style.filter = "brightness(100%)";
        }
    }

    const removeFromList = (id) => {
        setSuperhereosInformation(superhereosInformation.filter(s => s.id !== id));
        fetch(APIURL + "saves/remove", {
            method: "POST",
            headers: {
                "Content-Type": "application/json"
            },
            body: `{"token": "${localStorage.getItem("TOKEN")}", "superheroId": "${id}"}`
        });
    }

    if(noSaves) {
        return <h2>Collection is empty</h2>
    }

    if(loading) {
        return <h2>Loading...</h2>
    }

    return (
        <div className="collection">
            {superhereosInformation.map((s, i) => (
                <div className="superheroInfo" key={i}>
                    <img onClick={() => imageClicked(s.id)} id={"image" + s.id} alt="" src={s.image.url} />
                    <Link to={"/superhero/" + s.id}>
                        <p>{s.name}<i className="fa fa-level-up icon" style={{marginLeft: 8}}></i></p>
                    </Link>
                    <button onClick={() => removeFromList(s.id)} style={{display: "none"}} id={"removeBtn" + s.id} className="removeBtn">REMOVE</button>
                </div>
            ))}
        </div>
    );
}