import React from 'react';
import { Link } from 'react-router-dom';
import './SignUp.css';
import Notification from '../inc/Notification';
import { APIURL } from '../inc/Config';

export default class SignUp extends React.Component {
    state = {
        notificationProps: {
            opacity: 0,
            title: "",
            body: ""
        }
    }

    componentDidMount() {
        //Check if user is logged in
        if(localStorage.getItem("TOKEN"))
            window.location.href = "/";
            
        document.querySelector('.navbar').style.display = "none";
        document.querySelector('.footer').style.display = "none";
        document.title = "Sign Up";
        if(window.innerWidth <= 500)
            document.getElementById("sign_up_title").innerHTML = "Sign Up";
        
        window.addEventListener("resize", () => {
            if(document.getElementById("sign_up_title")) {
                if(window.innerWidth <= 500)
                document.getElementById("sign_up_title").innerHTML = "Sign Up";
            else
                document.getElementById("sign_up_title").innerHTML = "Sign Up To SuperhereosHub";
            }
        });
    }

    inputFocus = (key) => {
        document.getElementById(key + "_icon").style.color = "#40916C";
        document.getElementById(key + "_icon").style.borderColor = "#1B4332";
        document.getElementById(key + "_input").style.borderColor = "#1B4332";
    }

    inputBlur = key => {
        document.getElementById(key + "_icon").style.color = "black";
        document.getElementById(key + "_icon").style.borderColor = "black";
        document.getElementById(key + "_input").style.borderColor = "black";
    }

    signInFormSubmition = (e) => {
        e.preventDefault();

        const username = document.getElementById("username_input").value;
        const password = document.getElementById("password_input").value;
        const email = document.getElementById("email_input").value;
        document.getElementById("sign_in_button").innerHTML = "<i class='fa fa-spinner fa-spin icon'></i>";
        let isValid = true;
        let title = "Error";
        let body = "";


        if(!(/\S/.test(username) && /\S/.test(password) && /\S/.test(email))) {
            body = "Please fill required fields";
            isValid = false;
        }

        if(!this.validateEmail(email) && isValid) {
            body = "Please enter a valid email";
            isValid = false;
        }

        if((username.includes(" ") || password.includes(" ")) && isValid) {
            body = "Username and password shouldn't contain white spaces";
            isValid = false;
        }

        if(username.length > 10 && isValid) {
            body = "Username length should be less than 10";
            isValid = false;
        }

        if(password.length < 5 && isValid) {
            body = "Password length should be more than 5";
            isValid = false;
        }

        if(isValid) {
            fetch(APIURL + "users/signup", {
                method: "POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                body: `{"username": "${username}", "password": "${password}", "email": "${email}"}`
            })
                .then(res => res.json())
                .then(data => {
                    if(data.status === "fail") {
                        document.getElementById("sign_in_button").innerHTML = "Sign Up";
                        this.setState({notificationProps: {
                            opacity: 1,
                            title: "Error",
                            body: "Username is taken, please pick another one"
                        }});
                    } else {
                        localStorage.setItem("TOKEN", data.token);
                        window.location.href = "/";
                    }
                });
        } else {
            document.getElementById("sign_in_button").innerHTML = "Sign Up";
            this.setState({notificationProps: {
                opacity: 1,
                title: title,
                body: body
            }});
        }
    }

    validateEmail = (email) => {
        const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    }

    render() {
        return (
            <div className="sign_up_container">
                <Notification
                    xClicked={() => this.setState({
                            notificationProps: {
                                opacity: 0,
                                title: this.state.notificationProps.title,
                                body: this.state.notificationProps.body
                            }
                        }
                    )}
                    title={this.state.notificationProps.title}
                    body={this.state.notificationProps.body}
                    opacity={this.state.notificationProps.opacity}
                />
                <div className="main_container">
                    <h2 id="sign_up_title">Sign Up To SuperhereosHub</h2>
                    <form onSubmit={this.signInFormSubmition}>
                        <div>
                            <i id="username_icon" className="fa fa-user icon"></i>
                            <input onFocus={() => this.inputFocus("username")} onBlur={() => this.inputBlur("username")} type="text" id="username_input" />
                        </div>
                        <div>
                            <i id="email_icon" className="fa fa-envelope icon"></i>
                            <input onFocus={() => this.inputFocus("email")} onBlur={() => this.inputBlur("email")} type="email" id="email_input" />
                        </div>
                        <div>
                            <i id="password_icon" className="fa fa-lock icon"></i>
                            <input onFocus={() => this.inputFocus("password")} onBlur={() => this.inputBlur("password")} id="password_input" type="password" />
                        </div>
                        <button id="sign_in_button" type="submit">SIGN UP</button>
                        <Link to={"/user/signin"}>
                            Already have an account? Click here!
                        </Link>
                    </form>
                </div>
                <div className="right_container">
                   <h2>Create New Account!</h2>
                    <Link to={"/user/signin"}>
                        <button>SIGN IN</button>
                    </Link> 
                </div>
            </div>
        );
    }
}