import React, { useEffect, useContext, useState } from 'react';
import './Dashboard.css';
import { UserContext } from '../../context/User';
import Settings from './Settings';
import Colelction from './Collection';
import { Link } from 'react-router-dom';
import Collection from './Collection';

export default function Dashboard() {
    const user = useContext(UserContext)[0];
    const [currentView, setCurrentView] = useState("COLLECTION");

    useEffect(() => {
        document.getElementById("navbar").style.display = "none";
        document.title = "Dashboard";
    }, []);

    const sideButtonClicked = (btn1, btn2, key) => {
        document.getElementById(btn1).classList.add("active");
        document.getElementById(btn2).classList.remove("active");
        setCurrentView(key);
    }

    return (
        <div className="dashboard">
            <div className="section-a"></div>
            <img alt="" src={require("../../images/dashboard_images/user.png")} className="user_icon" />
            <p className="username">{user.username}</p>
            <div className="section-b">
                <div className="buttons">
                    <a href="#content">
                        <button id="btn1" className="active"  onClick={() => sideButtonClicked("btn1", "btn2", "COLLECTION")}>COLLECTION</button>
                    </a>
                    <a href="#content">
                       <button id="btn2" onClick={() => sideButtonClicked("btn2", "btn1", "SETTINGS")}>SETTINGS</button>
                    </a>
                    <Link to={"/"}>
                        <button>BACK TO HOME</button>
                    </Link>
                </div>
                <div style={{display: currentView === "COLLECTION" ? "block" : "none"}} id="content" className="content">
                    <Collection saves={user.saves} />
                </div>
                <div style={{display: currentView === "SETTINGS" ? "block" : "none"}} id="content" className="content">
                    <Settings username={user.username} email={user.email} />
                </div>
            </div>
        </div>
    );
}