import React, { useState } from 'react';
import { APIURL } from '../inc/Config';
import Notification from '../inc/Notification';

export default function Settings(props) {
    const [notificationOpacity, setNotificationOpacity] = useState(0);

    const signOutClicked = () => {
        localStorage.removeItem("TOKEN");
        window.location.href = "/";
    }

    const changePassClicked = () => {
        fetch(APIURL + "users/email/change_password/" + props.username, {
            method: "POST"
        });
        setNotificationOpacity(1);
    }

    return (
        <div className="settings">
            <Notification
                xClicked={() => setNotificationOpacity(0)}
                title={"Success"}
                body={"Check your email inbox"}
                opacity={notificationOpacity}
            />
            <div className="email">
                <i className="fa fa-envelope icon"></i>
                <p>{props.email}</p>
            </div>
            <div className="settings_buttons">
                <button onClick={() => changePassClicked()} >CHANGE PASS</button>
                <button onClick={() => signOutClicked()} className="second">SIGN OUT</button>
            </div>
        </div>
    )
}