import React, { useState, useEffect } from 'react';
import './Home.css';
import { Redirect, Link } from 'react-router-dom';

export default function Home() {
    const popularSearches = useState([
        {
            name: "Flash II",
            url: "https://www.superherodb.com/pictures2/portraits/10/100/892.jpg",
            id: "265"
        },
        {
            name: "Spider-Man",
            url: "https://www.superherodb.com/pictures2/portraits/10/100/133.jpg",
            id: "620"
        },
        {
            name: "Batman",
            url: "https://www.superherodb.com/pictures2/portraits/10/100/639.jpg",
            id: "70"
        },
        {
            name: "Nightwing",
            url: "https://www.superherodb.com/pictures2/portraits/10/100/851.jpg",
            id: "491"
        }
    ])[0];

    const [supermanIsMoving, setSupermanIsMoving] = useState(false);

    useEffect(() => {
        document.title = "Home";
        if(document.querySelector(".section-a-header")) {
            if(window.innerWidth < 500)
                document.querySelector(".section-a-header").innerHTML = "Search a superhero";
        }
        window.addEventListener("resize", () => {
            if(document.querySelector(".section-a-header")) {
                if(window.innerWidth < 500)
                    document.querySelector(".section-a-header").innerHTML = "Search a superhero";
                else
                    document.querySelector(".section-a-header").innerHTML = "Search your favorite superhero";
            }
        });
        if(document.querySelector(".navbar").style.display === "none") {
            document.querySelector(".navbar").style.display = "flex";
            document.querySelector(".footer").style.display = "block";
        }
    }, []);

    const slide_image_clicked = () => {
        if(!supermanIsMoving) {
            if(document.querySelector(".slide_image")) {
                document.querySelector(".slide_image").classList.add("move_superman");
                setSupermanIsMoving(true);   
            }
            setTimeout(() => {
                if(document.querySelector(".slide_image")) {
                    document.querySelector(".slide_image").classList.remove("move_superman");
                    setSupermanIsMoving(false);
                }
            }, 2000);
        }
    }

    const [redirect, setRedirect] = useState({
        redirect: false,
        params: ""
    });

    const formSubmition = (e) => {
        e.preventDefault();
        
        const searchReslt = document.getElementById("search_input").value;
        if (/\S/.test(searchReslt)) {
            setRedirect({
                redirect: true,
                params: "/search/" + searchReslt
            });
        }
    }

    if(redirect.redirect)
        return <Redirect to={redirect.params} />

    return (
        <div className="home">
            <div className="section-a">
                <div className="left">
                    <h2 className="section-a-header">Search your favorite superhero</h2>
                    <form onSubmit={formSubmition}>
                        <input id="search_input" type="text" />
                        <button>
                            <img alt="" src={require("../../images/icons/search_icon.png")} />
                        </button>
                    </form>
                </div>
                <div className="right">
                    <img onClick={() => slide_image_clicked()} className="slide_image" alt="" src={require("../../images/home_images/superman.png")} />
                </div>
            </div>
            <div className="section-b">
                <h2>POPULAR SUPERHEREOS</h2>
                <div className="popular_searches">
                    {popularSearches.map((p, i) => (
                        <Link to={"/superhero/" + p.id} key={i}>
                            <div className="popular_search">
                                <div>
                                    <img src={p.url} alt="" />
                                </div>
                                <p>{p.name}</p>
                            </div>
                        </Link>
                    ))}
                </div>
            </div>
        </div>
    );
}