import React, { useState, useEffect, useContext } from 'react';
import NotFound from '../inc/NotFound';
import { useParams } from 'react-router-dom';
import { APIURL } from '../inc/Config';
import { UserContext } from '../../context/User';
import Notification from '../inc/Notification';
import './Superhero.css';

export default function Superhero() {
    const [user, setUser] = useContext(UserContext);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(false);
    const [groupAffiliation, setGroupAffiliation] = useState([]);
    const [aliases, setAliases] = useState([]);
    const [relatives, setRelatives] = useState([]);
    const [saveBtnIsShown, setSaveBtnIsShown] = useState(false);
    const [notificationOpacity, setNotificationOpacity] = useState(0);
    const [notificationBody, setNotificationBody] = useState("");
    const [superhero, setSuperhero] = useState({
    });
    const [isSaved, setIsSaved] = useState(false);
    const id = useParams().id;

    useEffect(() => {
        if(superhero.name)
            setLoading(false);
    }, [superhero]);

    useEffect(() => {
        window.scrollTo(0, 0);
        document.getElementById("navbar").style.display = "none";
        document.title = "Dashboard";
        document.title = "Loading...";
        fetch(`https://www.superheroapi.com/api.php/222080678854229/${id}`)
            .then(res => res.text())
            .then(data => {
                let response = data.replace("full-name", "fullName");
                response = response.replace("place-of-birth", "placeOfBirth");
                response = response.replace("group-affiliation", "groupAffiliation");
                response = JSON.parse(response);
                if(response.response === "error") {
                    setError(true);
                    setLoading(false);
                } else {
                    document.title = response.name;
                    setSuperhero(response);
                    doubleCheck(response.connections.groupAffiliation.split(", "), response.connections.relatives.split("; "), response.biography.aliases);
                }
            })
    }, [id]);

    const logoClicked = () => {
        if(user.isLoggedIn) {
            if(!saveBtnIsShown) {
                document.getElementById("superheroImg").style.filter = "brightness(40%)";
                document.getElementById("saveBtn").style.filter = "brightness(100%)";
                document.getElementById("saveBtn").style.width = "60px";
                setSaveBtnIsShown(true);
            } else {
                document.getElementById("superheroImg").style.filter = "brightness(100%)";
                document.getElementById("saveBtn").style.filter = "brightness(0%)";
                document.getElementById("saveBtn").style.width = "0px";
                setSaveBtnIsShown(false);
            }
        }
    }
    
    const saveButtonClicked = () => {
        if(isSaved) {
            //Set New Saves Array
            setUser({
                username: user.username,
                isLoggedIn: user.isLoggedIn,
                email: user.email,
                saves: user.saves.filter(s => s.superheroId !== id)
            });

            //Server Request
            fetch(APIURL + "saves/remove", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: `{"token": "${localStorage.getItem("TOKEN")}", "superheroId": "${id}"}`
            });

            //Show Notification
            setNotificationBody(superhero.name + " removed from your collection");
        } else {
            //Set New Saves Array
            const saves = user.saves;
            saves.push({superheroId: id});
            setUser({
                username: user.username,
                isLoggedIn: user.isLoggedIn,
                email: user.email,
                saves
            });

            //Server Request
            fetch(APIURL + "saves/save", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json"
                },
                body: `{"token": "${localStorage.getItem("TOKEN")}", "superheroId": "${id}"}`
            });

            //Show Notification
            setNotificationBody(superhero.name + " added to your collection");
        }
        document.getElementById("superheroImg").style.filter = "brightness(100%)";
        document.getElementById("saveBtn").style.filter = "brightness(0%)";
        document.getElementById("saveBtn").style.width = "0px";
        setSaveBtnIsShown(false);
        setNotificationOpacity(1);
    }

    const doubleCheck = (gr, rel, al) => {
        let groupAffiliation = gr;
        let relatives = rel;
        let aliases = al;
        groupAffiliation.forEach((g, i) => {
            if(g.includes("; ")) {
                groupAffiliation.splice(i, 1);
                g.split("; ").forEach(t => groupAffiliation.push(t));
            }
        });
        aliases.forEach((g, i) => {
            if(g.includes("; ")) {
                aliases.splice(i, 1);
                g.split("; ").forEach(t => aliases.push(t));
            }
        });
        relatives.forEach((g, i) => {
            if(g.includes("), ")) {
            relatives.splice(i, 1);
            g.split("), ").forEach((t, i) => {
                if(i !== g.split("), ").length - 1)
                relatives.push(t + ")");
                else
                relatives.push(t);
            });
            }
        })
        setGroupAffiliation(groupAffiliation);
        setAliases(aliases);
        setRelatives(relatives);
    }

    //Check If Superhero Is Saved
    useEffect(() => {
        let superheroIsSaved = false;
        user.saves.forEach(s => {
            if(s.superheroId === id)
                superheroIsSaved = true;
        })
        setIsSaved(superheroIsSaved);
    }, [user]);

    if(loading) {
        return (
            <div className="superhero">
                <div className="section-a"></div>
                <div className="loading">
                    <h2>Loading...</h2>
                </div>
            </div>
        )
    }

    if(error) {
        return (
            <div className="superhero">
                <div className="section-a"></div>
                <NotFound />
            </div>
        )
    }

    return (
        <div className="superhero">
            <Notification
                xClicked={() => setNotificationOpacity(0)}
                title={"Success"}
                body={notificationBody}
                opacity={notificationOpacity}
            />
            <div className="container-a">
                <div className="section-a"></div>
                <img onError={() => document.getElementById("superheroImg").src = require("../../images/superhero_not_found.png")} onClick={() => logoClicked()} className="image" id="superheroImg" alt="" src={superhero.image.url} /> 
                <br />
                <img onClick={() => saveButtonClicked()} id="saveBtn" alt="" src={isSaved ? require("../../images/superhero_images/remove_from_collection.png") : require("../../images/superhero_images/add_to_collection.png")} className="save_btn" />
                <p>{superhero.name}</p>
            </div>
            <div className="container-b">
                <div className="table">
                    <div className="header">
                        <p>Stats</p>
                    </div>
                    <div className="body">
                        <p>Intelligence: {superhero.powerstats.intelligence}</p>
                        <p>Strength: {superhero.powerstats.strength}</p>
                        <p>Speed: {superhero.powerstats.speed}</p>
                        <p>Durability: {superhero.powerstats.durability}</p>
                        <p>Power: {superhero.powerstats.power}</p>
                        <p className="last">Combat: {superhero.powerstats.combat}</p>
                    </div>
                </div>
                <div className="table">
                    <div className="header">
                        <p>Family</p>
                    </div>
                    <div className="body">
                        {relatives.map((r, i) => (
                            <p className={i === relatives.length - 1 ? "last" : ""} key={i}>{r.charAt(0).toUpperCase() + r.slice(1)}</p>
                        ))}
                    </div>
                </div>
                <div className="table">
                    <div className="header">
                        <p>Aliases</p>
                    </div>
                    <div className="body">
                        {aliases.map((r, i) => (
                            <p className={i === aliases.length - 1 ? "last" : ""} key={i}>{r.charAt(0).toUpperCase() + r.slice(1)}</p>
                        ))}
                    </div>
                </div>
                <div className="table">
                    <div className="header">
                        <p>Afilliation</p>
                    </div>
                    <div className="body">
                        {groupAffiliation.map((r, i) => (
                            <p className={i === groupAffiliation.length - 1 ? "last" : ""} key={i}>{r.charAt(0).toUpperCase() + r.slice(1)}</p>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    )
}