import React, { useEffect, useState } from 'react';
import { useParams, Link } from 'react-router-dom';
import './SearchResults.css';

export default function SearchResults() {
    const { search_key } = useParams();
    const [searchResults, setSearchResults] = useState([]);
    const [resulstToShow, setResultsToShow] = useState([]);
    const [loading, setLoading] = useState(true);
    const [paginationValues, setPaginationValues] = useState({
        currentIndex: 1,
        maxIndex: 1
    });

    useEffect(() => {
        document.title = "Loading...";
        fetch("https://www.superheroapi.com/api.php/222080678854229/search/" + search_key)
            .then(res => res.json())
            .then(searchResults => {
                setLoading(false);
                if(searchResults.results) {
                    setSearchResults(searchResults.results);
                    setResultsToShow(searchResults.results.filter((s, i) => i < 20));
                    document.title = `Search results | ${searchResults.results.length}`;
                    if(searchResults.results.length !== 1) {
                        setPaginationValues({
                            currentIndex: 1,
                            maxIndex: Math.ceil((searchResults.results.length - 1) / 20)
                        });
                    }
                } else {
                    document.title = "Found nothing"
                }
            });
        if(document.querySelector(".navbar").style.display === "none") {
            document.querySelector(".navbar").style.display = "flex";
            document.querySelector(".footer").style.display = "block";
        }
    }, [search_key]);

    const setNotFoundImageSrc = (id) => {
        if(document.getElementById(id))
            document.getElementById(id).src = require("../../images/superhero_not_found.png");
    }

    const rightButtonPaginationClicked = () => {
        if(paginationValues.currentIndex !== paginationValues.maxIndex) {
            setResultsToShow(searchResults.filter((r, i) => i > paginationValues.currentIndex * 20 && i <= (paginationValues.currentIndex + 1) * 20));
            setPaginationValues({
                currentIndex: paginationValues.currentIndex + 1,
                maxIndex: paginationValues.maxIndex
            });
        }
    }

    const leftButtonPaginationClicked = () => {
        if(paginationValues.currentIndex !== 1) {
            setResultsToShow(searchResults.filter((r, i) => i > (paginationValues.currentIndex - 2) * 20 && i <= (paginationValues.currentIndex - 1) * 20));
            setPaginationValues({
                currentIndex: paginationValues.currentIndex - 1,
                maxIndex: paginationValues.maxIndex
            });
        }
    }

    if(!loading) {
        if(searchResults.length === 0) {
            return (
                <div className="search_results">
                    <h1>NOTHING FOUND</h1>
                </div>
            );
        } else {
            return (
                <div className="search_results">
                    <h2>Search Results for {search_key}</h2>
                    <div className="results">
                        {resulstToShow.map((s, i) => (
                            <Link key={i} to={"/superhero/" + s.id}>
                                <div className="result">
                                    <div>
                                        <img id={`search_image_${s.id}`} onError={() => setNotFoundImageSrc(`search_image_${s.id}`)} alt="" src={s.image.url} />
                                    </div>
                                    <p>{s.name}</p>
                                </div>
                            </Link>
                        ))}
                    </div>
                    <div className="pagination_div" style={{display: searchResults.length < 20 ? "none" : "flex"}}>
                        <button onClick={() => leftButtonPaginationClicked()}>
                            <a href="#navbar">
                                <img alt="" src={require("../../images/icons/arrow_left.png")} />
                            </a>
                        </button>
                        <p>{paginationValues.currentIndex}/{paginationValues.maxIndex}</p>
                        <button onClick={() => rightButtonPaginationClicked()}>
                            <a href="#navbar">
                                <img alt="" src={require("../../images/icons/arrow_right.png")} />
                            </a>
                        </button>
                    </div>
                </div>
            );
        }

    }

    return (
        <div className="search_results">
            <h2>LOADING...</h2>
        </div>
    );
}