import React, { useState, useContext, useEffect } from 'react';
import './Navbar.css';
import { UserContext } from '../../context/User';
import { Link } from 'react-router-dom';
import { APIURL } from '../inc/Config';

export default function Navbar() {
    const [siedebarVisible, setSiedebarVisible] = useState(false);
    const [user, setUser] = useContext(UserContext);

    const fetchUsers = () => {
        fetch(APIURL + "users/" + localStorage.getItem("TOKEN"))
            .then(res => res.json())
            .then(data => {
                if(data.status === "success") {
                    setUser({username: data.username, email: data.email, isLoggedIn: true, saves: data.saves});
                } else {
                    if(localStorage.getItem("TOKEN"))
                        localStorage.removeItem("TOKEN");
                }
            });
    }
    
    useEffect(() => {
        if(localStorage.getItem("TOKEN")) {
            fetchUsers();
        }
    }, []);

    const optionsClicked = () => {
        if(!siedebarVisible) {
            setSiedebarVisible(true);
            document.querySelector('.navbar .sidebar').style.display = "flex";
            setTimeout(() => {
                document.querySelector('.navbar .sidebar').style.opacity = "1";
            }, 100);
        } else {
            setSiedebarVisible(false);
            document.querySelector('.navbar .sidebar').style.opacity = "0";
            setTimeout(() => {
                document.querySelector('.navbar .sidebar').style.display = "none";
            }, 300);
        }
    }

    window.addEventListener("resize", () => {
        if(window.innerWidth > 500) {
            if(document.querySelector('.navbar .sidebar')) {
                document.querySelector('.navbar .sidebar').style.opacity = "0";
                setTimeout(() => {
                    setSiedebarVisible(false);
                    document.querySelector('.navbar .sidebar').style.display = "none";
                }, 300);
            }
        }
    });

    return (
        <div className="navbar" id="navbar">
            <div className="left">
                <img alt="" src={require("../../images/logo.png")} />
            </div>
            <div className="web right">
                <Link to={"/"}>
                    HOME
                </Link>
                <Link to={user.isLoggedIn ? "/user/dashboard" : "/user/signin"}>
                    {user.isLoggedIn ? "DASHBOARD" : "SIGN IN"}
                </Link>
            </div>
            <div className="right mobile">
                <div onClick={() => optionsClicked()} className="options">
                    <div className="line"></div>
                    <div className="line"></div>
                    <div className="line"></div>
                </div>
            </div>
            <div className="sidebar">
                <Link to={"/"}>
                    HOME
                </Link>
                <Link to={user.isLoggedIn ? "/user/dashboard" : "/user/signin"}>
                    {user.isLoggedIn ? "DASHBOARD" : "SIGN IN"}
                </Link>
            </div>
        </div>
    );
}