import React, { useState, useEffect } from 'react';

export default function Notification(props) {
    const [opacity, setOpacity] = useState(props.opacity)

    useEffect(() => {
        setOpacity(props.opacity);
    }, [props.opacity])

    const fade = () => {
        setOpacity(0);
        props.xClicked();
    }

    return (
        <div style={{opacity: opacity}} className="notification">
            <i onClick={() => fade()} className="fa fa-times icon"></i>
            <br />
            <h3>{props.title}</h3>
            <p>{props.body}</p>
        </div>
    )
} 